<template>
  <div>
    <div class="income container">
      <form>
        <label for="date">Select Date*</label>
        <input v-model="form.date" type="date" id="date" />
        <label for="sum">Type Sum*</label>
        <input
          v-model.number="form.sum"
          type="number"
          id="sum"
          placeholder="sum"
        />
        <label for="categories">Select Category*</label>
        <select v-model="form.category" id="categories">
          <option value="" disabled selected>categories</option>
          <option value="Food">Food</option>
          <option value="Clothes">Clothes</option>
          <option value="Games">Games</option>
          <option value="Technologies">Technologies</option>
        </select>
        <label for="comment">Type Comment</label>
        <textarea
          v-model="form.comment"
          style="bac"
          id="comment"
          cols="40"
          rows="5"
          placeholder="comment"
        ></textarea>
        <button
          type="submit"
          @keyup.enter="expensesBtn"
          @click="expensesBtn"
          class="saveBtn"
        >
          Save
        </button>
        <div @click="resetBtn" class="resetBtn">RESET</div>
      </form>

      <chart-js :expenses="expenses" class="chart"></chart-js>
    </div>
    <div class="chartList">
      <!-- <h1>Monitoring</h1> -->
      <ul v-for="(expense, expenses) in expenses" :key="expenses">
        <li>
          <p>Date:</p>
          <p>{{ expense.date }}</p>
        </li>
        <li>
          <p>Category:</p>
          <p>{{ expense.category }}</p>
        </li>
        <li>
          <p>Sum:</p>
          <p>{{ expense.sum }}</p>
        </li>
        <li class="last">
          <p>Comment:</p>
          <p>{{ expense.comment }}</p>
        </li>
        <div @click="removeList(expense)" class="deleteList">Remove</div>
        <!-- <hr /> -->
      </ul>
    </div>
  </div>
</template>
<script>
import ChartJs from "@/components/ChartJs.vue";
export default {
  components: { ChartJs },
  data() {
    return {
      form: { date: "", sum: "", category: "", comment: "" },
      expenses: [],
      total: 0,
    };
  },
  mounted() {
    if (localStorage.data) {
      this.expenses = JSON.parse(localStorage.data);
      console.log(this.expenses);
    }
  },
  methods: {
    expensesBtn() {
      event.preventDefault();
      if (
        this.form.date !== "" &&
        this.form.sum !== "" &&
        this.form.sum !== 0 &&
        this.form.category !== ""
      ) {
        this.expenses.push({ ...this.form });
      } else alert("All fields are required");
    },
    resetBtn() {
      this.expenses = [];
    },
    removeList(expense) {
      this.expenses.splice(this.expenses.indexOf(expense), 1);
    },
  },
};
</script>
<style lang="scss">
li {
  color: #fff;
}
.income {
  // margin: 0 auto;
  display: flex;
  justify-content: center;
  padding-top: 30px;
  margin-top: 30px;
  border-top: 1px solid grey;
  align-items: center;
  color: rgba(172, 255, 47, 0.473);

  form {
    display: flex;
    flex-direction: column;
    // align-items: center;
    width: 50%;
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
    ::-webkit-calendar-picker-indicator {
      filter: invert(1);
    }
    input {
      // background-color: #fff;
      height: 40px;
      border: none;
      color: grey;
      margin-bottom: 15px;
      padding-left: 10px;
      font-size: 18px;
      border-bottom: 1px solid grey;
      margin-top: 5px;
    }
    textarea {
      padding-left: 10px;
      font-size: 18px;
      margin-top: 5px;
      padding-top: 3px;
      resize: none;
    }
    select {
      // background-color: #fff;
      height: 40px;
      margin-bottom: 15px;
      padding-left: 10px;
      font-size: 18px;
      color: grey;
      border: unset;
      border-bottom: 1px solid grey;
      margin-top: 5px;
      option {
        // background-color: grey;
      }
    }
    #comment {
      // background-color: #fff;
      margin-top: 5px;
      color: #fff;
    }
    .saveBtn {
      color: #fff;
      font-size: 18px;
      padding: 7px 0;
      margin-top: 15px;
      cursor: pointer;
      background-color: rgb(59, 179, 59);
      color: #fff;
      text-transform: uppercase;
      text-align: center;
      transition: all 0.2s;
      border: none;
      &:active {
        background-color: green;
      }
    }
  }
  .resetBtn {
    background-color: red;
    color: #fff;
    font-size: 18px;
    padding: 7px 0;
    margin-top: 15px;
    border: none;
    cursor: pointer;
    text-align: center;
    transition: all 0.2s;
    &:active {
      background-color: rgb(163, 10, 10);
    }
  }
}
.chartList {
  // display: flex;
  // flex-direction: column;
  // justify-content: center;
  // align-items: center;
  width: 600px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 50px;
  margin-top: 80px;
  h1 {
    color: green;
    text-shadow: 10px 10px 25px rgb(31, 124, 52),
      -10px 10px 25px rgba(31, 124, 53, 0.247),
      -10px -10px 25px rgba(31, 124, 53, 0.247),
      10px -10px 25px rgba(31, 124, 53, 0.247);
    margin-bottom: 30px;
    margin-top: 50px;
  }
  ul {
    list-style-type: none;
    li {
      padding: 7px 10px;
      font-size: 20px;
      display: flex;
      justify-content: space-between;
      color: grey;
      border-bottom: 1px solid rgba(128, 128, 128, 0.521);
    }
    .last {
      border-bottom: none;
    }
    .deleteList {
      color: #fff;
      font-size: 16px;
      padding: 5px 0;
      background-color: red;
      cursor: pointer;
      transition: 0.1s;
      &:active {
        background-color: rgb(173, 9, 9);
      }
    }
  }
}
</style>
