<template>
  <div сlass="canvas">
    <canvas ref="canvas" id="myChart" width="100" height="100"></canvas>
  </div>
</template>
<script>
import { Pie } from "vue-chartjs";
export default {
  extends: Pie,
  props: ["expenses"],
  methods: {},

  data() {
    return {};
  },
  mounted() {},

  watch: {
    expenses(newVal) {
      const categories = {};

      newVal.forEach((expense) => {
        if (categories[expense.category])
          categories[expense.category] += expense.sum;
        else categories[expense.category] = expense.sum;
      });

      this.renderChart({
        labels: Object.keys(categories),

        datasets: [
          {
            label: "Расходы",
            data: Object.values(categories),
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(172, 255, 47, 0.473)",
            ],
            borderColor: [
              "rgba(255, 99, 132, 1)",
              "rgba(54, 162, 235, 1)",
              "rgba(255, 206, 86, 1)",
              "rgba(172, 255, 47, 1)",
            ],
            borderWidth: 1,
          },
        ],
      });

      localStorage.data = JSON.stringify(newVal);
    },
  },
};
</script>
<style lang="scss">
.chart {
  width: 400px;
  color: rgba(255, 0, 0, 0.427);
  margin: 0 auto;
  margin-top: 70px;
}
.canvas {
  width: unset;
}
</style>
